&-comment {
    position: relative;
    padding-top: 6rem;
    @include afterClear();

    &-header {
        padding-bottom: 2rem;
        font-size: 1.6rem; 
        color: #777;
        @include afterClear();

        .comment-count {
            float: left;
        }

        .comment-sort {
            float: right;

            li {
                float: left;

                &:first-child {
                    font-weight: bold;
                }

                &:last-child:before {
                    display: inline-block;
                    content: "|";
                    padding: 0 1rem;
                }
            }
        }
    }

    .comment-list {
        border: 1px solid #ccc;
        border-width: 1px 0;
        box-sizing: border-box;

        ul {
            li {
                position: relative;
                
                &:first-child {
                    margin-top: 2rem;
                }

                .comment-profile {
                    position: absolute;
                    top: 0;
                    left: 0;

                    .userImage {
                        width: 4rem;
                        height: 4rem;
                        font-size: 2rem;
                        line-height: 4rem;
                    }
                }

                .comment-modify {
                    margin-left: 6rem;
                    

                    textarea {
                        width: 100%;
                        height: 4.5rem;
                        min-height: 4.5rem;
                        padding: 1rem;
                        border-radius: .5rem;
                        border: 1px solid #ccc;
                        box-sizing: border-box;
                        outline: none;
                        resize: none;
                        font-size: 1.6rem;
                    }

                    &-btnGroup {
                        padding-top: 2rem;
                        text-align: right;

                        button {
                            padding: 1rem 1.5rem;
                            margin-right: 1rem;
                            border: 1px solid #ccc;
                            border-radius: .5rem;
                            background-color: #fff;
                            font-size: 1.4rem;
                            color: #333;
                            outline: none;
                            cursor: pointer;
                        
                            &:last-child {
                                margin-right: 0;
                            }
                        }

                        .btn-confirm {
                            background-color: #f00;
                            border: 1px solid #f00;
                            color: #fff;
                        }
                    }
                }

                .comment {
                    margin-left: 6rem;
                    padding-bottom: 2rem;
                    
                    
                    &-header {
                        font-size: 1.6rem;
                        font-weight: bold;
                        padding-bottom: .5rem;
                        cursor: pointer;
                    }

                    &-content {
                        font-size: 1.6rem;
                        line-height: 2rem;
                        padding-bottom: .5rem;
                        word-break: break-all;
                    }

                    &-footer {
                        @include afterClear();
                        padding-bottom: 2rem;
                        border-bottom: 1px solid #ccc;

                        &-left {
                            float: left;
                            font-size: 1.3rem;
                            color: #777;

                            span {
                                margin-right: 1rem;
                            }

                            .comment-reply-btn {
                                color: #333;
                                cursor: pointer;
                                
                            }
                        }

                        &-right {
                            float: right;
                            font-size: 1.4rem;

                            > div {
                                display: inline-block;
                                padding-right: 2rem;
                                cursor: pointer;

                                > i {
                                    padding-right: .5rem; 
                                }

                                > i:last-child {
                                    padding-right: 0;
                                }
                            }

                            > div:last-child {
                                padding-right: 0;
                            }
                        }
                    }
                }
            }

            > li:last-child {
                .comment { 
                    border: none;
                    padding-bottom: 0;

                    &-footer {
                        padding-bottom: 0;
                        border: none;
                    }
                }
            }

            //대댓글 영역
            ul li {
                border-bottom: 1px solid #ccc;

                .comment-footer {
                    border-bottom:  none;
                }
            }
        }

        //
        .more-cntReply {
            margin-top: 2rem;
            padding: 2rem;
            text-align: center;
            font-weight: bold;
            font-size: 1.4rem;
            border-top: 1px solid #ccc;
            cursor: pointer;
        }
        
    }
    .comment-reply {
        padding-top: 2rem;
        position: relative;
        cursor: pointer;

        &-profile {
            position: relative;
            
            .userImage {
                width: 4rem;
                height: 4rem;
                font-size: 2rem;
                line-height: 4rem;
            }
        }

        .comment {
            margin-left: 7rem;
        
            &-reply-editer {

                textarea {
                    width: 100%;
                    height: 4.5rem;
                    min-height: 4.5rem;
                    padding: 1rem;
                    border-radius: .5rem;
                    border: 1px solid #ccc;
                    box-sizing: border-box;
                    outline: none;
                    resize: none;
                    font-size: 1.6rem;
                }
            }

            &-reply-btnGroup {
                padding: 2rem 0;
                text-align: right;

                button {
                    padding: 1rem 1.5rem;
                    margin-right: 1rem;
                    border: none;
                    border-radius: .5rem;
                    background-color: red;
                    font-size: 1.4rem;
                    color: #fff;
                    outline: none;
                    cursor: pointer;
                
                    &:last-child {
                        margin-right: 0;
                    }
                }
            }
        }
    }
}